












export default {
	data() {
		return {
			merry: '圣诞节快乐~！'
		};
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		}
	},
	onLoad(e) {
		let info = JSON.parse(e.detailData);
		if (info.merry) {
			this.merry = info.merry;
		}
	},
	onShareAppMessage(res) {
		let title = this.merry;
		let detail = {
			merry: title
		}
		
		return {
			title: title,
			imageUrl: '../../static/img/4.png',
			path: '/pages/card-4/card-4?detailData=' + JSON.stringify(detail),
		}
	}
}



